html {
    font-size: 62.5%;
    line-height: 1.4;
}

body {
    font-size: 1.6rem;
    overflow: hidden;
    max-width: 100%;
    max-height: 100%;
}

html, body {
    height: 100%;
}

*:focus {
    outline: none;
}

[background-white], .background-white {
    background-color: #fff !important;
}

md-content[background-gray] {
    background-color: #ececec !important;
}

md-content[background-gray] {
    > p, > span, > div {
        color: #999;
    }
}

md-list-item .title {
    font-weight: 500;

    text-overflow: ellipsis;
    overflow: hidden
}

md-list-item > button {
    padding: 8px 16px;
}

md-list-item .md-caption {
    line-height: 16px;
}

